  height: 8px;
  border-right: 2px solid #FFF;
  border-bottom: 2px solid #FFF;
}

.market__filter {
  display: flex;
  justify-content: space-between;
  margin-bottom: 55px;
  padding: 0 239px;
  text-align: center;
}

.market__select .nice-select .list {
  width: 100%;
  margin-top: 0;
  color: #333;
  border-radius: 0;
  background-color: #DDD;
}

.market__nav a {
  line-height: 57px;
  display: block;
  height: 57px;
  padding: 0 47px;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  color: #FFFFFF;
  border-radius: 0 !important;
  background: #1752B4 none repeat scroll 0 0;
}
.market__nav a.active {
  color: #FFF;
  background: #729FEC none repeat scroll 0 0 !important;
}
.market__nav a:hover {
  color: #FFF;
  background: #729FEC none repeat scroll 0 0;
}

.sin__market__item.tab-pane:not(.active) {
  display: none;
}

.nice-select.open .list {
  overflow-y: visible;
  max-height: 200px;
}

.nice-select {
  overflow-y: visible;
  max-height: 200px;
}

