/*-------------------
	Banner Styles
----------------------*/
.single__account.tab-pane:not(.active) {
  display: none;
}

.acount__nav {
  margin-bottom: 60px;
  text-align: center;
  border-bottom: 2px solid #A3A3A3;
}
.acount__nav .nav-item {
  width: 50%;
}
.acount__nav .nav-item a {
  font-size: 28px;
  line-height: 50px;
  position: relative;
  padding: 15px;
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
  text-align: center;
  color: #A3A3A3;
}
.acount__nav .nav-item a:hover {
  color: #000;
}
.acount__nav .nav-item a.active {
  color: #000 !important;
  background-color: transparent !important;
}
.acount__nav .nav-item a.active::before {
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  content: "";
  background: #000 none repeat scroll 0 0;
}

.dg__account {
  width: 570px;
  margin: 0 auto;
}

.single__account .input__box span {
  font-size: 18px;
  display: block;
  margin-bottom: 15px;
  text-transform: capitalize;
  color: #333;
}
.single__account .input__box input {
  width: 100%;
  height: 50px;
  margin-bottom: 21px;
  padding: 0 15px;
  border: 1px solid #BEBEBE;
  outline: none;
}
.single__account .forget__pass {
  font-size: 18px;
  display: block;
  margin-bottom: 20px;
  text-align: right;
  color: #333;
}

.account__btn {
  font-size: 19px;
  display: block;
  width: 100%;
  height: 45px;
  padding: 0 8px;
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
  text-transform: capitalize;
  color: #FFF;
  border: 0 none;
  border-radius: 30px;
  background: #002556 none repeat scroll 0 0;
}
.account__btn:hover {
  color: #FFF;
  background: #F7A901 none repeat scroll 0 0;
}

/* Responsive Css*/
@media only screen and (max-width: 767px) {
  .dg__account {
    width: 502px;
  }
}
@media only screen and (max-width: 575px) {
  .dg__account {
    width: 400px;
  }
}
@media only screen and (max-width: 420px) {
  .dg__account {
    width: 300px;
  }
}
/*---------------------
		Footer Styles
-----------------------*/
.dg__footer__container {
  padding-top: 243px;
  padding-bottom: 80px;
}

.footer__widget h4 {
  font-size: 24px;
